"use client";

import React, { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { useAuthStore } from "@/store/auth-store";
import { useClientStore } from "@/store/client-store";
import { useProcessStore } from "@/store/process-store";
import { Card, CardHeader, CardTitle, CardContent, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
	Dialog,
	DialogContent,
	DialogDescription,
	DialogFooter,
	DialogHeader,
	DialogTitle,
	DialogTrigger,
} from "@/components/ui/dialog";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Badge } from "@/components/ui/badge";
import { Plus, Mail, Phone, User, FileText, Calendar, Eye } from "lucide-react";

const ClientesPage = () => {
	const router = useRouter();
	const { isAuthenticated, isAdmin } = useAuthStore();
	const { clients, fetchClients, addClient, isLoading } = useClientStore();
	const { getProcessesByClient } = useProcessStore();
	
	// Estados para Dialog de Novo Cliente
	const [openNewClient, setOpenNewClient] = useState(false);
	const [formData, setFormData] = useState({
		name: "",
		email: "",
		cpf: "",
		phone: "",
	});

	// Estados para Dialog de Ver Processos
	const [openViewProcesses, setOpenViewProcesses] = useState(false);
	const [selectedClient, setSelectedClient] = useState(null);
	const [clientProcesses, setClientProcesses] = useState([]);

	useEffect(() => {
		if (!isAuthenticated) {
			router.push("/login");
		} else if (!isAdmin()) {
			router.push("/dashboard");
		} else {
			fetchClients();
		}
	}, [isAuthenticated, isAdmin]);

	const handleSubmit = async (e) => {
		e.preventDefault();
		const result = await addClient(formData);
		if (result.success) {
			setFormData({ name: "", email: "", cpf: "", phone: "" });
			setOpenNewClient(false);
			await fetchClients();
		}
	};

	const handleViewProcesses = (client) => {
		setSelectedClient(client);
		const processes = getProcessesByClient(client.id);
		setClientProcesses(processes);
		setOpenViewProcesses(true);
	};

	return (
		<div className="flex flex-col gap-6">
			<div className="flex items-center justify-between border-b pb-4">
				<div>
					<h1 className="text-2xl font-bold">Clientes</h1>
					<p className="text-sm text-muted-foreground">
						Gerencie todos os clientes cadastrados
					</p>
				</div>
				
				{/* Dialog de Novo Cliente */}
				<Dialog open={openNewClient} onOpenChange={setOpenNewClient}>
					<DialogTrigger asChild>
						<Button>
							<Plus className="h-4 w-4" />
							Novo Cliente
						</Button>
					</DialogTrigger>
					<DialogContent className="max-w-2xl">
						<DialogHeader>
							<DialogTitle>Cadastrar Novo Cliente</DialogTitle>
							<DialogDescription>
								Preencha as informações do cliente para cadastrá-lo no sistema
							</DialogDescription>
						</DialogHeader>
						<form onSubmit={handleSubmit}>
							<div className="grid gap-4 py-4">
								<div className="grid grid-cols-2 gap-4">
									<div className="grid gap-2">
										<Label htmlFor="name">Nome Completo *</Label>
										<Input
											id="name"
											placeholder="Nome completo do cliente"
											value={formData.name}
											onChange={(e) =>
												setFormData({ ...formData, name: e.target.value })
											}
											required
										/>
									</div>
									<div className="grid gap-2">
										<Label htmlFor="email">Email *</Label>
										<Input
											id="email"
											type="email"
											placeholder="email@exemplo.com"
											value={formData.email}
											onChange={(e) =>
												setFormData({ ...formData, email: e.target.value })
											}
											required
										/>
									</div>
								</div>
								<div className="grid grid-cols-2 gap-4">
									<div className="grid gap-2">
										<Label htmlFor="cpf">CPF *</Label>
										<Input
											id="cpf"
											value={formData.cpf}
											onChange={(e) =>
												setFormData({ ...formData, cpf: e.target.value })
											}
											placeholder="000.000.000-00"
											required
										/>
									</div>
									<div className="grid gap-2">
										<Label htmlFor="phone">Telefone *</Label>
										<Input
											id="phone"
											value={formData.phone}
											onChange={(e) =>
												setFormData({ ...formData, phone: e.target.value })
											}
											placeholder="(00) 00000-0000"
											required
										/>
									</div>
								</div>
							</div>
							<DialogFooter>
								<Button
									type="button"
									variant="outline"
									onClick={() => setOpenNewClient(false)}
								>
									Cancelar
								</Button>
								<Button type="submit" disabled={isLoading}>
									{isLoading ? "Salvando..." : "Cadastrar Cliente"}
								</Button>
							</DialogFooter>
						</form>
					</DialogContent>
				</Dialog>
			</div>

			<div className="grid gap-4">
				{clients.map((client) => (
					<Card key={client.id} className="hover:shadow-md transition-shadow">
						<CardHeader>
							<div className="flex items-start justify-between">
								<div className="flex-1">
									<CardTitle className="text-lg mb-3">{client.name}</CardTitle>
									<div className="grid grid-cols-2 gap-3 text-sm">
										<div className="flex items-center gap-2 text-muted-foreground">
											<Mail className="h-4 w-4" />
											<span>{client.email}</span>
										</div>
										<div className="flex items-center gap-2 text-muted-foreground">
											<Phone className="h-4 w-4" />
											<span>{client.phone}</span>
										</div>
										<div className="flex items-center gap-2 text-muted-foreground">
											<User className="h-4 w-4" />
											<span>{client.cpf}</span>
										</div>
										<div className="flex items-center gap-2 text-muted-foreground">
											<FileText className="h-4 w-4" />
											<span>{client.processCount} processos</span>
										</div>
									</div>
								</div>
								<div className="flex gap-2">
									<Button variant="outline" size="sm">
										Ver Processos
									</Button>
								</div>
							</div>
						</CardHeader>
					</Card>
				))}
			</div>
		</div>
	);
};

export default ClientesPage;
